// components/Status.tsx
import { formatDate } from "../lib/date";
import styles from "../styles/Status.module.css";

interface StatusType {
  id: number;
  date: string;
  content: string;
}

interface Props {
  statuses: StatusType[];
}

export default function Status({ statuses }: Props) {
  return (
    <div className={styles.statusGrid}>
      {statuses.map((status) => {
        const date = new Date(status.date);
        const year = date.getFullYear() % 100; // Âêé‰∏§‰Ωç
        const month = date.toLocaleString("en-US", { month: "short" }); // Jan, Feb...
        return (
          <div key={status.id} className={styles.statusRow}>
            <div className={styles.colYear}>{year}</div>
            <div className={styles.colMonth}>{month}</div>
            <div className={styles.colContent}>{status.content}</div>
          </div>
        );
      })}
    </div>
  );
}
